<template>
<!-- 

    v-infinite-scroll="loadMore"
    :infinite-scroll-disabled="busy"
    :infinite-scroll-immediate="false"
    infinite-scroll-distance="200"
 -->
  <section
    class="search-result"
    ref="wrapper"
  >
    <header class="header">
      <h2 class="title">
        查询结果（共<span class="total">{{ total }}</span>条）
      </h2>
    </header>

    <ul class="result-list" v-if="total > 0">
      <li
        is="ResultItem"
        class="item"
        v-for="(item) of currentData"
        :key="item.id"
        :item="item"
        :keyword="currentKeyword"
      ></li>
    </ul>

    <!-- 无结果提示 -->
    <div class="result-empty" v-else>
      抱歉，找不到与您查询相符的记录
    </div>

    <!-- 上拉加载提示区 -->
    <template v-if="loading">
      <loading
        v-if="hasMore"
      />

      <no-more
        v-if="!hasMore"
      />
    </template>

  </section>
</template>

<script>
import {
  getElementDocPosition,
  getStyles,
  getScrollOffset,
  getViewportSize
} from 'utils/tools';

import ResultItem from './Item';
import Loading from './Loading';
import NoMore from './NoMore';

export default {
  name: 'SearchResult',
  components: {
    ResultItem,
    Loading,
    NoMore
  },
  data () {
    return {
      // wrapper页面的y轴位置
      wrapperPageTop: 0,
      // wrapper的高度
      wrapperHeight: 0,
      // 可视窗口高度
      viewportHeight: 0,
      $_handleScroll: null
    }
  },
  props: {
    results: {
      type: Array,
      default () {
        return [];
      }
    },
    total: {
      type: Number,
      default: 0
    },
    loading: Boolean,
    busy: Boolean,
    hasMore: Boolean,
  },
  computed: {
    // 当前页码需要显示的数据
    currentData () {
      const results = this.results;

      return results.reduce((prev, item) => {
        let data = item.data || [];
        prev = [...prev, ...data];
        return prev;
      }, []);
    },

    // 当前的关键字
    currentKeyword () {
      let results = this.results;

      return results[0]['keyword']
    }
  },
  methods: {
    initScroll () {
      const oWrapper = this.$refs.wrapper;

      // 获取wrapper的距离文档的位置
      if (this.wrapperPageTop <= 0) {
        const { top } = getElementDocPosition(oWrapper);
        this.wrapperPageTop = top ? top : 0;
      }

      // 获取可视窗口高度
      if (this.viewportHeight <= 0) {
        const { height } = getViewportSize();
        this.viewportHeight = height ? height : 0;
      }

      // 获取wrapper的实际高度
      // 每次数据变动后，height值需要重新计算
      const height = parseInt(getStyles(oWrapper, 'height'));
      this.wrapperHeight = height ? height: 0;

      // 监听浏览器滚动事件
      if (!this.$_handleScroll) {
        this.$_handleScroll = this.handleScroll.bind(this);
        document.addEventListener('scroll', this.$_handleScroll, false);
      }
    },

    /**
     * 浏览器滚动事件
     */
    handleScroll (ev) {
      const e = ev || window.event,
            { top } = getScrollOffset(e),
            viewportHeight = this.viewportHeight,
            wrapperPageTop = this.wrapperPageTop,
            wrapperHeight = this.wrapperHeight,
            hasMore = this.hasMore;

      // console.log(top)
      
      // console.log(top + viewportHeight, wrapperPageTop + wrapperHeight)

      if (hasMore) {
        if (top + viewportHeight > wrapperPageTop + wrapperHeight - 100) {
          this.loadMore(); // 触底加载更多数据
        }
      } else {
        if (top + viewportHeight > wrapperPageTop + wrapperHeight + 20) {
          // console.log('已无更多数据')
          this.loadMore();
        }
      }
    },

    /**
     * 加载更多数据
     */
    loadMore () {
      const hasMore = this.hasMore,
            busy = this.busy,
            loading = this.loading,
            currentKeyword = this.currentKeyword;
          
      if (!hasMore && busy) {
        return false;
      }
      this.$emit('loadMore', {
        keyword: currentKeyword,
        bounce: {
          x: 0,
          y: this.wrapperPageTop + this.wrapperHeight - this.viewportHeight
        }
      })
    }
  },
  watch: {
    results () {
      this.$nextTick(() => {
        this.$emit('changeBusy', false);
        this.initScroll();
      })
    }
  },
  // mounted () {
  // },

  beforeDestroy () {
    if (this.$_handleScroll) {
      document.removeEventListener('scroll', this.$_handleScroll, false);
      this.$_handleScroll = null;
    }
  }
}
</script>

<style lang="scss" scoped>
.search-result {
  // margin-bottom: .14rem;

  .header {
    .title {
      color: #999;
      font-size: .14rem;
      font-weight: normal;

      .total {
        color: #21acff;
      }
    }
  }

  .result-empty {
    padding: .45rem;
    font-size: .14rem;
    color: #999;
    text-align: center;
  }
}
</style>
