<template>
  <li class="item" @click="handleClick">
    <header class="item-header">
      <h3 class="company-name" v-once v-html="companyName"></h3>
      <a
        :to="link"
        class="btn-more"
      >
        查看证照<span class="icon el-icon-arrow-right"></span>
      </a>
    </header>
    <div class="info">
      <div class="row">
        <span class="label">证照类型：</span>
        <span class="value">{{ item.permitType }}</span>
      </div>
      <div class="row">
        <span class="label">统一社会信用代码：</span>
        <span class="value">{{ item.uniformSocialCreditCode }}</span>
      </div>
      <div class="row">
        <span class="label">证照编号：</span>
        <span class="value">{{ item.permitNumber }}</span>
      </div>
      <div class="row">
        <span class="label">颁发日期：</span>
        <span class="value">{{ item.permitIssuedTime | formatDate }}</span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  name: 'ResultItem',
  props: {
    // {
    //   bpId: string, // 经营许可证id
    //   oprName: string, // 经营者名称，公司名称
    //   permitIssuedTime: string, // 许可证签发时间
    //   permitNumber: string, // 许可证编号
    //   permitType: string, // 许可证种类中文名
    //   uniformSocialCreditCode: string, // 统一社会信用代码
    // }
    item: {
      type: Object,
      default () {
        return {}
      }
    },
    keyword: {
      type: String,
      default: ''
    }
  },
  computed: {
    // 需要高亮匹配的关键字
    // 仅匹配第一项
    companyName () {
      const keyword = this.keyword,
            { oprName = '' } = this.item,
            reg = new RegExp(this.keyword, 'i');

      return oprName.replace(reg, '<span class="keyword">' + keyword + '</span>');
    },

    // 查看证照 链接
    link () {
      const item = this.item;

      return item && item.certSeq
        ? `/permit/${item.certSeq}`
        : '/';
    }
  },
  filters: {
    // 格式化日期，移除后面的时分秒
    formatDate (date) {
      return date.split(' ')[0];
    }
  },
  methods: {
    handleClick () {
      this.$router.push(this.link);
    }
  }
}
</script>

