  background-color: #fff;
  box-shadow: 0 2px 32px 0 rgba(0,42,86,0.1);

  .item-header {
    display: flex;
    align-items: flex-start;
    padding: .15rem 0;
    border-bottom: 1px solid #eee;

    .company-name {
      flex: 1;
      padding-right: .05rem;
      color: #151515;
      font-weight: normal;
      font-size: .14rem;
      line-height: .16rem;

      /deep/.keyword {
        color: #21acff;
      }
    }

    .btn-more {
      font-size: .14rem;
      color: #21acff;
      line-height: .16rem;
    }
  }

  .info {
    padding: .15rem 0;

    .row {
      display: flex;
      align-items: flex-start;

      .label {
        color: #999;
        font-size: .15rem;
        line-height: .225rem;
        word-break: keep-all;
      }

      .value {
        color: #151515;
        font-size: .15rem;
        line-height: .225rem;
        word-break: break-all;
      }

      & + .row {
        margin-top: .17rem;
      }
    }
  }
}
</style>
