<template>
  <div class="page-home">
    <common-header></common-header>

    <div class="page-container">
      <search-form
        :keyword="keyword"
        @changeKeyword="changeKeyword"
        @onSearch="onSearch"
      />

      <!-- 功能说明 -->
      <declaration v-if="!isSearch" />

      <search-result
        v-loading="loading"
        v-if="isSearch && keyword"
        :results="results"
        :total="total"
        :busy="busy"
        :loading="hasMoreLoading"
        :hasMore="hasMore"
        @loadMore="loadMore"
        @changeBusy="changeBusy"
      />
    </div>

    <common-footer />
  </div>
</template>

<script>
import { getPermitList } from 'models/Api';
import { getScrollOffset, sleep } from 'utils/tools';

import CommonHeader from 'components/Sub/Header';
import CommonFooter from 'components/Sub/Footer';
import SearchForm from 'components/Home/SearchForm';
import Declaration from 'components/Home/Declaration';
import SearchResult from 'components/Home/SearchResult';

export default {
  name: 'Home',
  components: {
    CommonHeader,
    CommonFooter,
    SearchForm,
    Declaration,
    SearchResult
  },
  data () {
    return {
      title: '东莞市电子证照公示查验系统',
      keyword: '', // 关键字、测试值 91440101MA5CLGPK81
      loading: false,
      // 搜索结果集
      // [
      //   {
      //     page: 1,
      //     keyword: 'xxxxx',
      //     data: [
      //       {
      //         bpId: string, // 经营许可证id
      //         oprName: string, // 经营者名称，公司名称
      //         permitIssuedTime: string, // 许可证签发时间
      //         permitNumber: string, // 许可证编号
      //         permitType: string, // 许可证种类中文名
      //         uniformSocialCreditCode: string, // 统一社会信用代码
      //       }
      //     ]
      //   },
      //   ...
      // ]
      results: [],
      total: 0, // 匹配关键字的结果总条目数
      page: 1, // 当前页码
      isSearch: false, // 控制搜索结果的显示与隐藏

      // 当关键字符合以下条件时，进行搜索拦截
      // 1、低于2个长度（不包括2个）；
      // 2、仅为“中国”、“广东”、“广东省”、“东莞”、“东莞市”、“公司”、“有限公司”
      keywordRules: {
        minLength: 2,
        words: ['中国', '广东', '广东省', '东莞', '东莞市', '公司', '有限公司']
      },

      hasMore: true, // 是否还有更多数据
      busy: true, // 上拉加载是否繁忙
      hasMoreLoading: false, // 上拉加载loading
    }
  },
  methods: {
    /**
     * 改变搜索关键字
     * @param { String } keyword - 搜索关键字
     */
    changeKeyword (keyword) {
      this.keyword = keyword;
    },

    /**
     * 切换页码
     * @param { String } page - 页码
     */
    // changePage (page) {
    //   this.page = page;
    //   this.onSearch();
    // },

    /**
     * 关键字校验
     * @param { string } keyword - 关键字
     * @return { Promise<boolean> }
     */
    checkKeyword (keyword) {
      return new Promise((resolve, reject) => {
        let {
          minLength,
          words
        } = this.keywordRules;

        if (keyword.length < minLength) {
          resolve(false);
        }

        if (words.includes(keyword)) {
          resolve(false);
        }

        resolve(true);
      });
    },

    /**
     * 更改busy状态
     */
    changeBusy (busy = false) {
      this.busy = busy
    },

    // 
    /**
     * 加载更多数据
     * @param { object } options
     * @param { string } options.keyword
     * @param { { x: number, y: number } } options.bounce
     */
    async loadMore (options) {
      const {
        keyword,
        bounce
      } = options;

      // console.log(bounce)

      if (this.busy) { // 正在加载其它数据
        return;
      }

      this.changeBusy(true);
      this.hasMoreLoading = true;
      this.page ++;
      await this.changeKeyword(keyword); // 恢复keyword，防止用户加载更多时，手动改变了关键字
 
      // 已无更多数据
      if (!this.hasMore) {
        await sleep(1000);
        // setTimeout(() => {
        // }, 1500)

        // console.log(getScrollOffset(), bounce)


        const currentScrollTop = getScrollOffset().top;

        if (currentScrollTop > bounce.y) {
          window.scrollTo(bounce.x, bounce.y); // 回弹效果
        }

        this.changeBusy(false);
        this.hasMoreLoading = false;
        return false;
      }

      const {
        current,
        records,
        total
      } = await getPermitList({
        current: this.page,
        size: 10,
        word: keyword
      }).catch((err) => {
        // 请求出错
        console.log(err);

        // this.results = [];
        // this.total = 0;
        // this.page = 1;
        // this.loading = false;
        // this.hasMore = false;
        this.page--; // 出错时恢复page
        this.changeBusy(false);
      });

      if (records.length > 0) {
        this.results.push({
          keyword,
          data: records,
          page: this.page
        });
      } else {
        this.hasMore = false; // 已无更多数据
      }

      this.hasMoreLoading = false;
      this.changeBusy(false)
    },

    // 执行搜索
    async onSearch () {
      const keyword = this.keyword;

      let page = this.page;

      if (!keyword) {
        // 当用户输入的关键字为空、且执行搜索功能时
        // 理应回到最初始的界面
        // 清空相关的搜索数据
        this.results = [];
        this.total = 0;
        this.page = 1;
        this.isSearch = false;
        return;
      }
      
      const checked = await this.checkKeyword(keyword);

      if (!checked) { // 关键字校验不通过，不执行搜索
        this.$alert('请输入更为详细的查询条件', '请重新查询', {
          confirmButtonText: '确定',
          // dangerouslyUseHTMLString: true,
          // showClose: false,
          center: true,
        })
        return;
      }
      
      // 当前页码数据存在、不再发起请求
      if (this.results.find((item) => item.page === page && item.keyword === keyword)) {
        return;
      }

      // 当前请求的keyword和result里面存储的keyword值不同,重置result
      if (this.results.find((item) => item.keyword !== keyword)) {
        this.page = 1;
        page = 1;
        this.results = [];
      }

      // 开启loading
      this.loading = true;

      this.isSearch = true;

      // 搜索类型
      // 1. 证照编码
      // 2. 统一社会信用代码
      // 3. 公司主体

      const {
        current,
        records,
        total
      } = await getPermitList({
        current: page,
        size: 10,
        word: keyword
      }).catch((err) => {
        // 请求出错
        console.log(err);

        this.results = [];
        this.total = 0;
        this.page = 1;
        this.loading = false;
      });
      
      this.total = total;

      if (records.length <= 0 || total <= 0) {
        this.results = [];
      } else {
        this.results.push({
          page,
          keyword,
          data: records
        });
      }
      
      this.loading = false;
      this.busy = false;
      
    },
  }
}
</script>

<style lang="scss" scoped>
.page-home {
  height: 100%;

  .page-container {
    min-height: calc(100% - 1.6rem);
    padding-top: .3255rem;
    padding-bottom: .15rem;
    box-sizing: border-box;
  }
}
</style>
