<template>
  <section class="search-form-wrapper">
    <el-form
      class="search-form clearfix"
      @submit.native.prevent="onSearch"
    >
      <input
        class="search-input"
        type="text"
        :placeholder="placeholder"
        v-model.trim="innerKeyword"
      /><!-- 移除autofocus -->
      <button
        type="submit"
        class="btn-search"
        v-stat="statInfo"
      >
        <span class="icon"></span>
        <span class="text">搜索</span>
      </button>
    </el-form>
  </section>
</template>

<script>
import { STAT_ACTION } from 'config/stat.config.js';

export default {
  name: 'SearchForm',
  props: {
    keyword: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      placeholder: '输入主体名称、相关编码等'
    }
  },
  computed: {
    innerKeyword: {
      get () {
        return this.keyword;
      },

      set (value) {
        // 关键字最多只支持输入100个字符
        if (value !== this.keyword && value.length <= 100) {
          this.$emit('changeKeyword', value);
        }
      }
    },

    // 埋点数据
    statInfo () {
      const keyword = this.keyword;

      return !keyword
        ? null
        : {
          action: STAT_ACTION.SEARCH,
          // 操作附带信息
          movementInfo: keyword,
        }
    }
  },
  methods: {
    // 搜索按钮点击事件
    onSearch () {
      this.$emit('onSearch');
    }
  }
}
</script>

